<template>
  <div class="pageTwo">
    <ul class="listsBox">
      <li v-for="item in listsData" :key="item.id">
        <span class="pic"><img :src="item.img" alt=""/></span>
        <div class="baseInfo">
          <div>
            <p><span>「标题」</span>{{ item.title }}</p>
            <p><span>「简介」</span>{{ item.intro }}</p>
            <p><span>「时间」</span>{{ item.date }}</p>
            <p><span>「技术栈」</span>{{ item.tech }}</p>
          </div>
          <button class="btn" type="button" @click="showFn(item.id)">
            查看详情
          </button>
        </div>
      </li>
    </ul>
    <drawer :videoUrlId="videoInfo" />
  </div>
</template>

<script>
import drawer from "@/components/drawer.vue";
import { mapMutations, mapState } from "vuex";
export default {
  name: "pageTwo",

  data() {
    return {
      listsData: [],
      videoInfo: ""
    };
  },
  components: {
    drawer
  },
  computed: {
    ...mapState(["videoStatus"])
  },
  methods: {
    ...mapMutations(["changeVideoStatus"]),
    showFn(id) {
      this.videoInfo = id;
      //   this.videoInfo.status = this.changeVideoStatus(true);
      this.changeVideoStatus(true);
      //   this.$store.commit("changeVideoStatus", true);
    }
  },
  mounted() {
    this.http.get("/data/secondPage.json").then(res => {
      this.listsData = res.data.lists;
    });
  }
};
</script>
