<template>
  <div class="pageOne">
    <p class="info">{{ introShow }}</p>
  </div>
</template>
<script>
export default {
  name: "pageOne",
  data() {
    return {
      intro: "",
      introShow: "",
      num: 0,
      timer: null
    };
  },
  methods: {
    showIntro() {
      let len = this.intro.length;
      if (this.num <= len) {
        this.introShow = this.intro.slice(0, this.num++) + "_";
        this.timer = setTimeout(this.showIntro, 150);
      } else {
        clearTimeout(this.timer);
        //使用replace需要一个变量接收，否则不会有效果
        this.introShow = this.introShow.replace("_", "");
      }
    }
  },
  mounted() {
    this.http.get("/data/firstPage.json").then(res => {
      this.intro = res.data.intro;
      this.showIntro();
    });
  }
};
</script>
<style lang="less" scoped>
.pageOne {
  background: url("https://img3.doubanio.com/view/activity_page/raw/public/p4640.jpg");
  height: 100%;
